.about__container {
	display: grid;
	grid-template-columns: 35% 50%;
	gap: 15%;
}

.about__me {
	width: 100%;
	aspect-ratio: 1/1;
	border-radius: 2rem;
	background: linear-gradient(
		45deg, 
		transparent, 
		var(--primary-text), 
		transparent);
	display: grid;
	/* place-items: center; */
}

.about__me-image {
	border-radius: 2rem;
	overflow: hidden;
	transform: rotate(15deg);
	transition: var(--transition);
}

.about__me-image:hover{
	transform: rotate(0deg);
}


.about__cards {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap:  1.5rem;
}

.about__card {
	background: var(--secondary-bg);
	border: 1px solid transparent;
	border-radius: 1rem;
	padding: 2rem;
	text-align: center;
	transition: var(--transition);
}



.about__card:hover {
	background: transparent;
	color: var(--primary-text);
	border: 0.1rem solid var(--primary-text);
	cursor: default;
}

.about__icon {
	color: var(--primary-text);
	font-size: 1.4rem;
	margin-bottom: 1rem;
}

.about__card h5 {
	font-size: 0.95rem;
}

.about__card small {
	font-size: 0.7rem;
	color: var(--color-white);
}

.about__info p {
	margin: 2rem 0 2.6rem;
	color: var(--color-white);
}




/* ******** MEDIA QUERIES (MEDIUM DEVICES)   ******** */

@media screen and (max-width: 1024px) {
	.about__container {
		grid-template-columns: 1fr;
		gap: 0;
	}

	.about__me {
		width: 50%;
		margin: 2rem auto 4rem;
	}

	.about__info p {
		margin: 1rem 0 1.5rem;
	}

	
}


/* ******** MEDIA QUERIES (SMALL DEVICES)   ******** */

@media screen and (max-width: 600px) {
	.about__me {
		width: 65%;
		margin: 0 auto 3rem;
	}

	.about__cards {
		grid-template-columns: 1fr 1fr;
		gap: 1rem;
	}

	.about__card {
		padding: 1rem;
	}

	.about__icon {
		margin-bottom: 0.5rem;
	}

	.about__info {
		text-align: center;
	}
	
	.about__info p {
		margin: 1.5rem 0;
		text-align: left;
	}

	}
 